.CurrentUser {
  &__title {
    font-size: 1rem;
    color: white;

    span {
      background-color: #4d457b;
      padding: 5px;
      border-radius: 5px;
    }
  }

  &__name {
    margin: 30px 0 10px;
    font-size: 1.5rem;
  }

  &__email,
  &__phone {
    color: #c4bdd9;
    text-align: center;
    margin: 5px;
  }

  &__button {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 15px;
  }
}
